import { Input, Col, Row, Button, Tabs, TabPane } from 'element-ui'

export default {
    components: {
        'el-input': Input,
        'el-col': Col,
        'el-row': Row,
        'el-button': Button,
        'el-tabs': Tabs,
        'el-tab-pane': TabPane
    },
    props: ['data_user', 'data_list'],
    data() {
        return {
            loading: false,
            user: {
                wechat: {
                    sex: ''
                }
            },
            activeName: '',
            fields: [],
            list: this.data_list,
            data: ''
        }
    },
    methods: {
        getHealthFields() {
            axios.post('api/health/fields').then(response => {
                this.fields = response.data.data
            });
        },
        getHealthDetail() {
            axios.post('api/health/getDetail', {health_id: this.list.id}).then(response => {
                this.data = response.data.data
                this.loading = false
            });
        }
    },
    watch: {
        data_user() {
            this.user = this.data_user
        },
        data_list() {
            this.list = this.data_list
            this.getHealthDetail()
            this.loading = true
        }
    },
    mounted() {
        this.getHealthFields()
    },
}



